import {
	mapGetters,
	mapMutations
} from "vuex"
export default {
	data() {
		return {
			cur: -1,
			unCheck: '../../static/img/mine/checked-false.png',
			check: '../../static/img/mine/checked-true.png',
			reasonReturn:[
				{
					title1:"我要退款",
					title2:"没收到货，或与卖家协商同意不用退货只退款",
					selected:false,
				},
				{
					title1:"我要退货退款",
					title2:"已收到货，需要退还收到的货物",
					selected:false,
				}
			],
			maskFlag: false
		};
	},
	computed:{
		...mapGetters({
			getReturnOrder:'getReturnOrder'
		})
	},
	onLoad() {
			console.log(this.getReturnOrder);
	},
	methods: {
		// 退款
		Return(){
			let data ={
				order_id:this.getReturnOrder.order_id,
				type:1,
				express_number:""
			}
			this.$api.orderRefund(data).then(res=>{
					if(res.data.errno == 0){
						this.close('refund');
						uni.showToast({
							title: '订单退款成功',
							duration: 1000,
							icon: "none"
						});
						setTimeout(res => {
							uni.switchTab({
								url: '/pages/order/order'
							})
						}, 1000)
						console.log(res);
					}else{
						this.close('refund');
						uni.showToast({
							title: '订单退款失败',
							duration: 1000,
							icon: "none"
						});
					}
			})
		},
		// 选中
		checked(item,i){
			console.log(item);
			this.reasonReturn.map((item,index)=>{
				if(index == i){
					item.selected = true
				}else{
					item.selected = false
				}
			})
			if(i == 0){
				this.$refs['refund'].open();
			}else{
				this.$refs['return'].open();
			}
		},
		close(name) {
			this.$refs[name].close()
		},
		toEditReturn() {
			uni.navigateTo({
				url: '/pages/order/editReturn'
			})
		}
	}
}
